@use '../variables' as v;

.navbar{
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 20px;
    padding-top: 10px;

    #page-logo{
        align-items: flex-start;
        height: 35px;
    }

    #navbar-links{
        display: flex;
        flex-direction: row;
        list-style-type: none;

        li{
            padding: 0px 0px 0px 27px;
            font-size: 15px;
            cursor: pointer;

            &:hover{
                color: v.$Soft-red
            }
        }
    }

    .drawer{
        background-color: white;
        .icon-menu{
            width: auto;
            height: auto;
            border: none;
            cursor: pointer;
        }
    }
}

.list-item{
    cursor: pointer;
    
    &:hover{
        color: v.$Soft-red;
    }
}